.setting-item[data-v-401d221c] {
  height: 1.6rem;
}
.setting-item:last-child .setting-name[data-v-401d221c] {
  border-bottom: none;
}
.xicon-setting[data-v-401d221c] {
  width: 0.53rem;
  height: 1.6rem;
  float: left;
  background-repeat: no-repeat;
  background-position: 0 center;
  margin-right: 0.1rem;
}
.story[data-v-401d221c] {
  background-image: url(../../assets/xicon-story.png);
}
.collect[data-v-401d221c] {
  background-image: url(../../assets/xicon-collect.png);
}
.honor[data-v-401d221c] {
  background-image: url(../../assets/xicon-up-setting.png);
}
.ask[data-v-401d221c] {
  background-image: url(../../assets/xicon-answer.png);
}
.set[data-v-401d221c] {
  background-image: url(../../assets/xicon-set.png);
}
.setting-name[data-v-401d221c] {
  float: left;
  width: 8.53rem;
  color: #333;
  height: 1.6rem;
  line-height: 1.65rem;
  font-size: 0.45rem;
  border-bottom: 1px solid #e0e0e0;
  background: url(../../assets/go.png) right center no-repeat;
}



